@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+TC:wght@100&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.myBar {
  color: black;
  background-color: #b9dea9;
  font-family: "Noto Sans TC", sans-serif !important;
}
.mItem {
  height: 5vh;
  margin-bottom: 1vh;
  clear: both;
  float: left;
  background-color: #cee7c2;
  border-radius: 3vh;
  display: grid;
  place-items: center;
}
.mItem li {
  padding: 10px;
  width: auto;
  overflow: hidden;
  font-size: 1rem;
  box-sizing: border-box;
  font-family: "Noto Sans TC", sans-serif !important;
  font-weight: 400;
  line-height: 1.5;
}
.menuList {
  margin-top: 5px;
  margin-left: 10px;
  background-color: black;
  height: 0;
}
.title {
  font-family: "Noto Sans TC", sans-serif !important;
  font-weight: 600;
}
.App {
  background: url(/static/media/bg.4597eb8b.png) no-repeat;
  background-size: cover;
  height: 100vh;
  width: 100vw;
}
.Container {
  height: 60vh;
  width: 60vw;
  background-color: white;
  opacity: 0.8;

  border-radius: 3vh;
  display: grid;
  place-items: center;
}
.content {
  margin-top: 15vh;
  display: grid;
  place-items: center;
}
.Container img {
  opacity: 1;
}

